@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap");
@font-face {
  font-family: "icons";
  font-display: swap;
  src: url("../fonts/icons.woff") format("woff"), url("../fonts/icons.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "pexel";
  src: url("../../fonts/NeuePixelSans.ttf");
}
@font-face {
  font-family: "TTSquares";
  src: url("../../fonts/TTSquaresCondensed-Regular.ttf");
  font-style: normal;
  font-weight: 500;
}
@font-face {
  font-family: "TTSquares";
  src: url("../../fonts/TTSquaresCondensed-Bold.ttf");
  font-style: normal;
  font-weight: 700;
}
@font-face {
  font-family: "TTSquares";
  src: url("../../fonts/TTSquaresCondensed-Black.ttf");
