}

.link-blue {
  color: #BB8E34;
}

.bg-zero {
  background-color: #ffffff !important;
}
.bg-zero :focus {
  box-shadow: none !important;
}

._focus {
  background: #ffffff;
}

._hidden {
  display: none;
  opacity: 0;
}

._animation-item {
  opacity: 0;
  transition: all 0.8s ease;
}
@media (min-width: 768px) {
  ._animation-item {
    transform: translate(0, 120%);
  }
}

._animation-active {
  opacity: 1 !important;
}
@media (min-width: 768px) {
  ._animation-active {
    transform: translate(0, 0);
  }
}

._dismissable-hidden {
  visibility: hidden;
}
@media (max-width: 1200px) {
  ._dismissable-hidden {
    display: none !important;
  }
}

.button-dismiss {
  background: transparent;
  font-size: 20px;
  color: #FF6464;
}

.row:after {
  display: block;
  content: "";
  clear: both;
}

.rub:after {
  content: "₽";
}

ol.counter {
  list-style-type: none;
  counter-reset: item;
}
ol.counter li {
  position: relative;
  padding: 0px 0px 0px 45px;
}
ol.counter li:before {
  counter-increment: item;
  content: counter(item);
  position: absolute;
  left: 0;
  top: 0;
  color: #818181;
  font-size: 14px;
  font-weight: 700;
  text-align: center;
  line-height: 26px;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  border: 1px solid #4274bb;
}

.ellipsis {
  display: block;
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.es {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.table {
  display: table;
  font-size: 0;
  width: 100%;
}

.trow {
  display: table-row;
}

.cell {
  display: table-cell;
}
.cell.full {
  width: 100%;
}

._ibg {
  position: relative;
}
._ibg img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

body.ie ._ibg {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
body.ie ._ibg img {
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}

._video {
  position: relative;
  overflow: hidden;
  height: 0;
  padding-bottom: 56.25%;
}
._video video,
._video iframe,
._video object,
._video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.videobg video,
.videobg iframe,
.videobg object,
.videobg embed {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  transform: translateX(-50%) translateY(-50%);
  background-size: cover;
}

._more-content {
  transition: all 0.3s ease 0s;
  overflow: hidden;
}

._more-link {
  cursor: pointer;
}
._more-link span {
  font-style: normal;
}
._more-link span:first-child {
  display: block;
}
._more-link span:last-child {
  display: none;
}
._more-link._active span {
  font-style: normal;
}
._more-link._active span:first-child {
  display: none;
}
._more-link._active span:last-child {
  display: block;
}

._swiper {
  overflow: hidden;
}
._swiper .swiper-wrapper {
  width: 100%;
  height: 100%;
  box-sizing: content-box;
  display: flex;
  position: relative;
}
._swiper.swiper-container-vertical .swiper-wrapper {
  flex-direction: column;
}
._swiper.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
}

.swiper-container-initialized .swiper-slide {
  flex-shrink: 0;
}

.swiper-container-android .swiper-slide,
.swiper-container-android .swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-button-lock {
  display: none !important;
}

._tabs-block {
  display: none;
}
._tabs-block._active {
  display: block;
}

.mirror {
  transform: scale(-1, 1);
}

.nicescroll-rails {
  z-index: 1000 !important;
}

._custom-scroll {
  position: fixed;
  right: 0;
  top: 0;
  width: 3px;
  height: 100%;
  z-index: 1000;
}
._custom-scroll__line {
  background-color: rgba(0, 0, 0, 0.5);
  border-radius: 3px 0 0 3px;
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
}

.popup {
  -webkit-overflow-scrolling: touch;
  z-index: 100;
  padding: 30px 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  transition: visibility 0.8s ease 0s;
  visibility: hidden;
  display: flex;
  justify-content: center;
}
.popup.show {
  visibility: visible;
  overflow: hidden;
}
.popup.show::before {
  opacity: 1;
}
.popup.show .popup__body {
  transform: scale(1);
}
.popup._active {
  overflow: hidden;
  visibility: visible;
}
.popup._active::before {
  opacity: 1;
}
.popup._active .popup__body {
  transition: all 0.3s ease 0.2s;
  transform: scale(1);
}
.popup__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
  flex: 1 1 auto;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #D2FD7E;
  border-radius: 24px;
  backdrop-filter: blur(27.1828px);
  padding: 20px 0;
}
.popup__body {
  transform: scale(0);
  transition: all 0.3s ease 0s;
  padding: 0 25px 30px;
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 24px;
}
.popup__close {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  border: 1px dashed rgba(0, 0, 0, 0.137476);
  position: absolute;
  align-self: end;
  justify-self: end;
  top: 2%;
  right: 2%;
  cursor: pointer;
  z-index: 30;
  background: url("../img/icons/close.svg") 0 0 no-repeat;
}
.popup__header {
  display: flex;
}

#popup__bg {
  content: "";
  background-color: rgba(0, 0, 0, 0.9);
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  display: none;
  transition: opacity 0.8s ease 0s;
}

#popup__bg.bg_active {
  opacity: 1;
  display: block;
}

.screen__first {
  background: #D2FD7E;
  backdrop-filter: blur(27.1828px);
  border-radius: 24px;
  width: 450px;
  padding: 0 25px 30px;
}
@media (max-width: 500px) {
  .screen__first {
    width: 96vw;
    padding: 10px;
    margin: 0 auto;
  }
}
@media (max-width: 500px) and (max-width: 400px) {
  .screen__first {
    width: 94vw;
  }
}

.popup__title {
  font-family: "pexel";
  font-size: 42px;
  line-height: 48px;
  margin-bottom: 20px;
}
@media (max-width: 650px) {
  .popup__title {
    margin-bottom: 20px;
  }
}

.popup__text {
  font-family: "Inter", sans-serif;
  font-size: 16px;
  line-height: 21px;
  opacity: 0.4;
  margin-bottom: 32px;
  text-align: center;
}
@media (max-width: 650px) {
  .popup__text {
    margin-bottom: 16px;
  }
}

.heroes {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 6px;
  margin-bottom: 24px;
}
@media (max-width: 650px) {
  .heroes {
    margin-bottom: 12px;
  }
}
.heroes .hero__img {
  margin-right: 20px;
}
.heroes .hero {
  display: flex;
  align-items: center;
  justify-content: center;
