  width: 100%;
  backface-visibility: hidden;
  transition: all 0.4s ease-out;
  display: flex;
  flex-direction: column;
}
.find .find__items .single-house .front .house__name {
  text-align: left;
  padding: 0 30px;
  flex-grow: 1;
  display: flex;
  align-items: center;
}
.find .find__items .single-house .front img {
  max-width: 100%;
}
.find .find__items .single-house .front .house__info {
  display: flex;
  border-top: 1px solid #E4E4E4;
}
.find .find__items .single-house .front .house__info .info__item {
  flex: 1 1 30%;
  height: 60px;
  display: flex;
  gap: 8px;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  font-weight: 700;
  border-right: 1px solid #E4E4E4;
}
.find .find__items .single-house .front .house__info .info__item p {
  margin-bottom: 6px;
}
.find .find__items .single-house .front .house__info .info__item:last-child {
  border-right: none;
}
.find .find__items .single-house .back {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  backface-visibility: hidden;
  padding: 40px;
  transition: all 0.4s ease-out;
  border-top: 1px solid rgba(17, 20, 19, 0.89);
  border-radius: inherit;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.find .find__items .empty__warning {
  color: rgba(2, 2, 0, 0.85);
  text-align: center;
}
.find #loadMore {
  margin-top: 8%;
  align-self: center;
  cursor: pointer;
}
@media (max-width: 650px) {
  .find #loadMore {
    margin-top: 4%;
  }
}
.find #loadMore:hover {
  background-color: #000000;
  color: #FFFfff;
}
.find .unhide {
  opacity: 0;
  border: 0;
  animation-name: a1;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
@keyframes a1 {
  to {
    opacity: 1;
  }
}
.find .back {
  transform: rotateY(180deg);
}
.find .single-house:hover .front {
  transform: rotateY(180deg);
}
.find .single-house:hover .back {
  transform: rotateY(360deg);
}
.find .wrapOn {
  justify-content: flex-start;
  gap: 40px;
}
.find .wrapOn .find__item {
  text-align: center;
}
.text__section {
  background: #000;
  text-align: center;
}
.text__section .text__section__container {
  padding: 150px 30px 150px;
}
@media (max-width: 768px) {
  .text__section .text__section__container {
    padding: 75px 2% 75px;
  }
}
.text__section .text__section__content {
  color: #FFFfff;
  max-width: 500px;
  margin: 0 auto;
  gap: 50px;
}
.text__section .text__section__content .text__section__title {
  font-size: 50px;
}
.text__section .text__section__content .button {
  align-self: center;
}

.clients__say .clients__say__container {
  padding: 100px 30px 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .clients__say .clients__say__container {
    padding: 50px 2% 10px;
  }
}
.clients__say .clients {
  display: flex;
  justify-content: space-between;
  overflow: auto;
}
.clients__say .clients .client {
  flex: 1 0 280px;
  gap: 20px;
  padding: 46px 40px;
  align-items: center;
  transition: 0.4s;
}
.clients__say .clients .client .clients__description .client__name {
  margin-bottom: 4px;
  font-weight: 700;
  font-size: 15px;
}
.clients__say .clients .client .clients__description .client__role {
  color: #979797;
}
.clients__say .clients ._active {
  background: #000;
}
.clients__say .clients ._active .client__name {
  color: #FFFfff;
}
.clients__say .feedback__items {
  height: 56vh;
  margin-bottom: 18%;
}
.clients__say .feedback__items .feedback__item {
  margin: 0 auto;
