}

.charity {
  margin-top: 120px;
  position: relative;
}
@media (max-width: 768px) {
  .charity {
    margin-top: 30px;
  }
}
.charity #bg__dark {
  position: absolute;
  width: 100%;
  height: 50%;
  bottom: 0;
  background: #212529;
}
.charity .charity__container {
  max-width: 1100px;
  background: #006FF9;
  position: relative;
  padding: 76px 100px;
  text-align: center;
  overflow: hidden;
}
.charity .charity__container:before {
  content: "";
  position: absolute;
  top: -22%;
  right: -4%;
  width: 188px;
  height: 226px;
  border-radius: 38px;
  transform: rotate(-127deg);
  background: #ffffff;
  opacity: 0.1;
}
.charity .charity__container:after {
  content: "";
  position: absolute;
  bottom: -22%;
  left: -4%;
  width: 188px;
  height: 226px;
  border-radius: 38px;
  transform: rotate(46deg);
  background: #FFFfff;
  opacity: 0.1;
  z-index: 2;
}
.charity .charity__title {
  color: #FFFfff;
  margin-bottom: 40px;
}
.page {
  flex: 1 1 auto;
}

